    .colaborations__list {
        max-width: 583px;
    }

    .colaborations__list-item {
        margin-bottom: 30px;
    }

    .colaborations-title {
        letter-spacing: 0.1em;
        width: max-content;
    }
    .star {
        top: 24px;
        left: 337.5px;
    }
    .cbg {
      height: 1100px;
    }
}

@media(max-width:960px) {
    .colaborations-title {
