.questions {
    position: relative;
    margin-top: 338px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    /* background:url(../../assets//images/questionsbg.png);
    background-repeat: no-repeat;
    background-size:cover; */
}

.questionsbg {
    position: absolute;
    top: -170px;
    width: 100%;
    /* z-index: 0; */
}

.questions-title {
    position: relative;
    font-family: 'TTOctosquares';
    font-style: normal;
    font-weight: 400;
    font-size: 64px;
    line-height: 100%;
    text-align: center;
    letter-spacing: 0.21em;
    max-width: 538px;
    align-items:center;
    text-align: center;
    z-index: 1;
}

.FAQ {
    position: absolute;
    left: 245px;
    top: -65px;
    font-family: 'MazzardH';
    font-style: normal;
    font-weight: 700;
    font-size: 15px;
    line-height: 140%;
    background: linear-gradient(90deg, #FFB800 0%, #FFBAC3 70.62%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    letter-spacing: normal;
/* text-fill-color: transparent; */
}

.lineFAQ {
    position: absolute;
    left: 210px;
    top: -41px;
    width: 122.5px;
    height: 10px;
    transform: rotateX(180deg);
}

.sun {
    margin: 0 20px 0 5px;
}

.questions__grid {
    background-color: blue;
    margin-top: 90px;
    margin-left: 10%;
    margin-right: 6%;
    margin-bottom: 357px;
    display: grid;
    grid-template-areas: 
    "g1 g1 g1 g2 g2 g2 g3 g3 g3"
    "g1 g1 g1 g2 g2 g2 g3 g3 g3"
    "g1 g1 g1 g2 g2 g2 g3 g3 g3"
    "g1 g1 g1 g7 g7 g7 g3 g3 g3"
    "g4 g5 g6 g7 g7 g7 g8 g8 g8"
    "g9 g9 g9 g7 g7 g7 g8 g8 g8"
    "g9 g9 g9 g7 g7 g7 g8 g8 g8"
    "g9 g9 g9 g10 g10 g10 g8 g8 g8"
    "g9 g9 g9 g10 g10 g10 g8 g8 g8"
    "g9 g9 g9 g10 g10 g10 g11 g11 g11"
    "g13 g13 g13 g10 g10 g10 g11 g11 g11"
    "g13 g13 g13 g12 g14 g14 g11 g11 g11"
    "g13 g13 g13 g12 g14 g14 g11 g11 g11"
    "g13 g13 g13 g12 g14 g14 g11 g11 g11"
    ;
    grid-template-columns: repeat(9, 1fr);
    grid-template-rows: repeat(13, 1fr);
    -webkit-clip-path: polygon(0 0, 5% 0, 8% 3%, 100% 3%, 100% 97%, 95% 97%, 92% 100%, 0 100%);
    clip-path: polygon(0 0, 5% 0, 8% 3%, 100% 3%, 100% 97%, 95% 97%, 92% 100%, 0 100%);
    background: linear-gradient(238.33deg, rgba(255, 255, 255, 0) -7.92%, rgba(255, 255, 255, 0.15) 33.87%);
    backdrop-filter: blur(16.9248px);
    z-index: 1;
}

.questions__grid::after {
    content: '';
    position: absolute;
    left: 8.5px;
    bottom: 8.5px;
    -webkit-clip-path: polygon(0 0, 0% 100%, 100% 100%);
    clip-path: polygon(0 0, 0% 100%, 100% 100%);
    padding: 7.5px;
    background: white;
}

.questions__grid::before {
    content: '';
    position: absolute;
    right: 7.5px;
    top:32.5px;
    -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%);
    clip-path: polygon(0 0, 100% 0, 100% 100%);
    padding: 7.5px;
    background: white;
}

.questions__grid h4 {
    font-family: 'TTOctosquares';
    font-style: normal;
    font-weight: 400;
    font-size: 31px;
    line-height: 117.8%;
    letter-spacing: 0.09em;
    text-transform: uppercase;
    margin-bottom: 16px;
}
.questions__grid p {
    font-family: 'MazzardH';
}

.questions__grid>div {
  padding-left: 29px;
  position: relative;
}

.questions__grid>div span {
    font-weight: bold;
}

