@media(max-width:1260px) {
    .questions__grid {
        grid-template-areas: 
        "g1 g1 g1 g1 g1 g1 "
        "g1 g1 g1 g1 g1 g1 "
        "g4 g4 g5 g5 g6 g6 "
        "g3 g3 g3 g3 g3 g3 "
        "g3 g3 g3 g3 g3 g3 "
        "g13 g13 g13 g13 g13 g13 "
        "g7 g7 g7 g7 g7 g7 "
        "g7 g7 g7 g7 g7 g7 "
        "g12 g12 g14 g14 g14 g14 "
        "g11 g11 g11 g11 g11 g11 "
        "g11 g11 g11 g11 g11 g11 ";
        grid-template-columns: repeat(6, 100px);
        grid-template-rows: repeat(11, 1fr);
        clip-path: polygon(0 0, 10% 0, 15% 1%, 100% 1%, 100% 99%, 90% 99%, 85% 100%, 0 100%);
        -webkit-clip-path: polygon(0 0, 10% 0, 15% 1%, 100% 1%, 100% 99%, 90% 99%, 85% 100%, 0 100%);
    }

    .g1 {
        border-right: none;
    }
    .g3 {
        padding-bottom: 0;
    }

    .g6 {
        border-right: none;
    }
    .g13 {
        border-right: none;
        border-bottom: 1px solid white;
        padding-top: 20px;
    }
    .g7 {
        border-right: none;
        border-bottom: 1px solid white;
        padding-top: 0;
    }
    .g14 {
        border-right: none;
        border-bottom: 1px solid white;
    }
    .g12 {
        
        border-bottom: 1px solid white;
    }
    .questions__grid::before {
        top: 20px;
    }

    .g2, .g8, .g9, .g10, .g13 {
        display: none;
    } 
}

@media(max-width:768px) {
    .questions__grid {
        grid-template-areas: 
        "g1 g1 g1 "
        "g1 g1 g1  "
        "g4 g5 g6 "
        "g3 g3 g3  "
        "g3 g3 g3 "
        "g13 g13 g13  "
        "g7 g7 g7  "
        "g7 g7 g7  "
        "g12  g14 g14  "
        "g11 g11 g11  "
        "g11 g11 g11  ";
        grid-template-columns: repeat(3, 100px);
        margin-left: auto;
        margin-right: auto;
        margin-bottom: 230px;
    }

    .questions__grid>div {
        padding-left: 10px;
    }

    .questions__grid h4 {
        font-size: 21px;
    }

    .g14 img {
        margin-right: 6px;
    }

    .questions-title {
        font-size: 35px;
    }

    .questions-title>span {
        padding-left: 0 !important;
    }
    .sun {
        width: 30px;
        height: 30px;
        margin-left: 10px;
    }

    .FAQ {
        left: 44.8%;
    }

    .lineFAQ {
        left: 38%;
    }
}

@media(max-width:430px) {
