.modal {
    height: 100vh;
    width: 100vw;
    background: gainsboro; /* background-color: rgba(184, 100, 100, 0.955); */
    position: fixed;
    color: white;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 1s;
    z-index: 7;
}

.modal.active {
   opacity: 1;
   pointer-events:all;
}

.modal__content {
    height: 100%;
    width: 100vw;
    padding: 55px 154px 0 154px;
    z-index: 5;
    transition: .75s;
}

.modal__badlands {
    background-image: url(../../images/factionModal/Badlands/bgBadlands.png);
    background-size: cover;
}

.modal__glacial {
    background-image: url(../../images/factionModal/Glacial/bgGlacial.png);
    background-size: cover;
}

.modal__sundered {
    background-image: url(../../images/factionModal/Sundered/bgSundered.png);
    background-size: cover;
}

.modal__skycitadel {
    background-image: url(../../images/factionModal/SkyCitadel/bgSkyCitadel.png);
    background-size: cover;
}